.wrapper {
    display: flex;
    height: 60px;
    max-width: 1020px;
    align-items: center;
    border-bottom: solid 6px #323746;
    background-color: rgb(54, 93, 152);
    margin: 0 auto;
}

.logo {
    width: 5%;
    margin-left: 3%;
}

.logo img {
    width: 100%;
    font-size: 24px;
    color: #ffffff;
    text-transform: uppercase;
    letter-spacing: 0.1px;
    cursor: pointer;
}

.nav_wrapper {
    width: 20%;
}

.nav_wrapper ul {
    display: flex;
    justify-content: space-around;
}

.nav_item {
    font-size: 28px;
}

.nav_icon {
    height: 18px;
    width: 18px;
    cursor: pointer;
}

.login_wrapper {

}

.login_wrapper a {
    color: aliceblue;
}
